<template>
    <transition mode="out-in" name="slide-fade" appear>
        <footer :class="{ 
            'dividend-page': this.isMobile && this.$store.getters.dataLoaded && this.$store.getters.loadData['Stock Type'] === 'Dividend' ||
            !this.isMobile && this.$store.getters.dataLoaded && this.$store.getters.loadData['Stock Type'] === 'Dividend' ||
            this.isMobile && this.$store.getters.dataLoaded && this.$store.getters.loadData['Stock Type'] === 'Non-Dividend',
            '' : !this.$store.getters.dataLoaded ||
            !this.isMobile && this.$store.getters.dataLoaded && this.$store.getters.loadData['Stock Type'] === 'Non-Dividend',
        }">
            <div class="px-3 px-lg-4 py-2">
                <div class="row">
                    <div class="col-12 col-lg-6 order-1 order-lg-2">
                        <div class="text-center d-sm-flex align-items-center justify-content-center justify-content-lg-end">
                            <p class="d-block my-2 me-2"></p>
                            <div class="kb-club">
                                <a target="blank" href="https://512kb.club"><span class="kb-club-no-bg">512KB Club</span><span class="kb-club-bg">Blue Team</span></a>
                            </div>
                        </div>
                    </div>
                    <div class="col-12 col-lg-6 text-center text-lg-start order-2 order-lg-1">
                        <p class="my-2">2022 &copy; RUBANERO</p>
                    </div>
                </div>
            </div>
        </footer>
    </transition>
</template>
<script>
export default {
    data(){
        return {
            isMobile: null,
        };
    },
    mounted(){
        // logic to activate this function whenever screen is resized
        window.addEventListener("resize", this.checkIsMobile);
    },
    created(){
        // logic to activate this function whenever screen is resized
        window.addEventListener("resize", this.checkIsMobile);
    },
    unmounted(){
        // logic to deactivate this function whenever screen is resized
        window.removeEventListener("resize", this.checkIsMobile);
    },
    computed: {
        dataLoaded(){
            return this.$store.getters.dataLoaded;
        },
    },
    methods:{
        checkIsMobile(){
            console.log(window.innerWidth);
            if(window.innerWidth < 992){
                this.isMobile = true;
            } else {
                this.isMobile = false;
            }
        },
    },
};
</script>
<style scoped>
footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 0;
}

footer.dividend-page {
    position: static;
}

.kb-club a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
    font-family: sans-serif;
}

.kb-club-bg,
.kb-club-no-bg {
    font-size: 14px;
    padding: .25rem .5rem;
    border: 2px solid #64B5F6;
}
.kb-club-no-bg {
    background: transparent;
    color: #ccc;
    border-radius: 5px 0 0 5px;
}

.kb-club-bg {
    background: #64B5F6;
    color: #333;
    border-radius: 0 5px 5px 0;
}

.light .kb-club-no-bg {
    background: transparent;
    color: #333;
    border: 2px solid #003479;
}
.light .form-validation-alert {
    color: #333;
}

.light .kb-club-bg {
    background: #003479;
    border: 2px solid #003479;
    color: #ccc;
}
</style>
